import Vue from 'vue'
import VueRouter from 'vue-router'

import home from './pages/Home'

Vue.use(VueRouter)

document.title = 'Colin Clisson'

export default new VueRouter({
    mode: `history`,
    routes: [
        {
            path: `*`,
            name: `home`,
            component: home
        }
    ]
})
