<template>
    <div :class="isMobileDevice() ? 'mobile-menu' : 'menu'">
        <a @click.prevent="scroll('top')" href="#">
            <b-icon icon="house"></b-icon>
            <span>Accueil</span>
        </a>
        <a @click.prevent="scroll('profile')" href="#">
            <b-icon icon="person"></b-icon>
            <span>Profil</span>
        </a>
        <a href="#">
            <b-icon icon="book"></b-icon>
            <span>Expériences</span>
        </a>
    </div>
</template>

<script>
    import { isMobileDevice } from '../helpers/utils'

    export default {
        name: "SmartMenu",
        methods: {
            scroll(name) {
                document.getElementById(name).scrollIntoView({behavior: "smooth"})
            },
            isMobileDevice() {
                return isMobileDevice();
            }
        }
    }
</script>

<style lang="scss" scoped>
    .menu {
        list-style: none;
        display: flex;
        flex-direction: column;
        align-items: center;
        background: linear-gradient(to right, rgb(255, 255, 255) 0%, rgb(237, 237, 237) 100%);
        border-radius: 30px;
        box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.25);
        text-align: left;
        font-size: 20px;
        width: 52px;
        transition: width 200ms ease;
        letter-spacing: 1px;

        a {
            color: #df006a;
            filter: grayscale(1.0);
            transition: 300ms;
            display: flex;
            align-items: center;
            height: 3rem;
            text-decoration: none;
            margin-right: auto;
            width: 100%;

            &:hover {
                filter: grayscale(0);
                background: linear-gradient(to right, rgba(0, 0, 0, 0.05) 0%, rgba(0, 0, 0, 0.01) 100%);

                &:first-child {
                    border-top-left-radius: 30px;
                    border-top-right-radius: 30px;
                }

                &:last-child {
                    border-bottom-left-radius: 30px;
                    border-bottom-right-radius: 30px;
                }
            }

            svg {
                margin: 0 0.8em;
            }

            span {
                display: none;
                padding-right: 15px;
            }
        }

        &:hover {
            width: 180px;

            a {
                text-decoration: none;

                span {
                    display: block;
                }
            }
        }
    }

    .mobile-menu {
        position: fixed;
        width: 100%;
        height: 50px;
        font-size: 25px;
        background: linear-gradient(to right, rgb(255, 255, 255) 0%, rgb(237, 237, 237) 100%);
        display: flex;
        align-items: center;
        flex-direction: row;
        justify-content: space-around;

        a {
            color: #df006a;
            filter: grayscale(1.0);
            transition: 300ms;
            flex-grow: 10;
            text-align: center;

            &:active {
                svg {
                    background: rgba(0, 0, 0, 0.1);
                    border-radius: 50%;
                    padding: 10px;
                }
            }

            span {
                display: none;
            }

            svg {
                transition: padding 200ms ease;
            }
        }
    }
</style>
